import {Dispatch} from 'redux';
import {appActions} from 'AppWithRedux/appSlice';

export const handleServerAppError = (data: any, dispatch: Dispatch) => {
    if (data.messages.length) {
        dispatch(appActions.setAppError({error: data.messages[0]})); //       
    } else {
        dispatch(appActions.setAppError({error: 'some error occured'})); //      
    }
    dispatch(appActions.setAppStatus({status: 'failed'})); //  
};

export const handleServerNetworkError = (error: { message: string }, dispatch: Dispatch) => {
    dispatch(appActions.setAppError({error: 'some error occured, CATCH'})); //   
    dispatch(appActions.setAppStatus({status: 'failed'})); //  
};
